.force-graph-container {
  width: 100%;
  height: 100vh;
  background-color: #000011;
  position: relative;
}

/* Tooltip styles */
.graph-tooltip {
  position: absolute;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  border-radius: 4px;
  font-size: 12px;
  max-width: 300px;
  z-index: 1000;
  pointer-events: none;
  display: none;
}

.graph-tooltip.visible {
  display: block;
} 